.Board {
  height: 450px;
  width: 450px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: auto;
  padding: 5px;
}

h1 {
  text-align: center;
  color: #005562;
  font-weight: 600;
}

.reset-btn {
  flex: 1;
  border: 1px solid #005562;
  border-radius: 5px;
  padding: 5px;
  box-shadow: 1px 1px 3px #009fb7;
  height: 40px;
  margin-top: 20px;
  background-color: #009fb7;
  color: white;
  font-weight: 600;
  font-size: 18px;
}

.reset-btn:hover {
  cursor: pointer;
  background-color: #005562;
}
